import UniIcon from "@/components/uni-icon/uni-icon.vue"
import CommApi from "@/pages/comm/api.js"
import ContentInfo from "@/pages/comm/content-info.vue"

export default {
	data() {
		return {
			talkList: [],
			total: 0,
			screenHeight: null,
			scrollTop: 0,
			page: 1,
			old: {
				scrollTop: 0
			},
			value: false,
			inputData: {},
			userinfo: {},
			userType: null,
			teacherInfo: {},
			replyInfo: '',
		}
	},

	components: {
		UniIcon,
		ContentInfo,
	},

	created() {
		this.userinfo = uni.getStorageSync("userinfo")
		this.userType = uni.getStorageSync("userType")
		const code = this.$route.query && this.$route.query.code;
		const state = this.$route.query && this.$route.query.state;
		this.getTeacherInfo();
		uni.getSystemInfo({
			success: (res) => {
				this.screenHeight = res.screenHeight - 192;
				console.log(this.screenHeight);
			}
		})
		this.getTalkList();
	},

	methods: {
		submitMessage(e) {
			console.log(this.value)
		},
		checkboxChange(e) {
			var items = this.items,
				values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if (values.includes(item.value)) {
					this.$set(item, 'checked', true)
				} else {
					this.$set(item, 'checked', false)
				}
			}
			console.log(this.items)
		},
		upper: function(e) {
			console.log(e)
		},
		lower: function(e) {
			if (this.talkList.length < this.total) {
				this.page = this.page + 1;
				this.getMoreTalkList()
			}
		},
		scroll: function(e) {
			// console.log(e, e.detail.scrollTop)
			this.old.scrollTop = e.detail.scrollTop
		},
		changeReply(obj) {
			if (obj.replyContent != null) obj.webState = !obj.webState;
		},
		talkClick() {
			this.value = true;
		},
		getTeacherInfo() {
			const day = new Date();
			const currentYear = day.getFullYear();
			let currentMonth = null;
			const monthCount = day.getMonth() + 1;
			if (monthCount < 10) {
				currentMonth = '0' + monthCount;
			} else {
				currentMonth = monthCount;
			}
			const currentDay = day.getDate();
			const date = `${currentYear}-${currentMonth}-${currentDay}`;
			uni.request({
				url: CommApi.api.getTeacherInfo.real,
				data: JSON.stringify({
					zbrq: date,
				}),
				method: 'POST',
				header: {
					'content-type': 'application/json',
					"Access-Control-Allow-Origin": '*',
				},
				success: (res) => {
					if (res.statusCode === 200 && res.data.code === 1) {
						// this.swiperImages = res.data.data;
						this.teacherInfo = res.data.data;
						this.replyInfo = `我是本周值班教师${this.teacherInfo && this.teacherInfo.name}，请问您想和我分享什么`;
					} else {
						uni.showToast({
							title: res.data && res.data.msg || res.data.error,
							icon: "none"
						});
					}
				}
			});
		},
		getTalkList() {
			uni.request({
				url: CommApi.api.getTalkList.real,
				data: JSON.stringify({
					studentId: (this.userinfo && this.userinfo.id) || 63,
					page: this.page,
					pageSize: 10,
				}),
				method: 'POST',
				header: {
					'content-type': 'application/json',
					"Access-Control-Allow-Origin": '*',
				},
				success: (res) => {
					if (res.statusCode === 200 && res.data.code === 1) {
						const talkList = res.data.data;
						this.total = res.data.total;
						const len = talkList.length;
						for (let i = 0; i < len; i++) {
							talkList[i].webState = false;
						}
						this.talkList = talkList;
					} else {
						uni.showToast({
							title: res.data && res.data.msg || res.data.error,
							icon: "none"
						});
					}
				}
			});
		},
		getMoreTalkList() {
			uni.request({
				url: CommApi.api.getTalkList.real,
				data: JSON.stringify({
					studentId: (this.userinfo && this.userinfo.id) || 63,
					page: this.page,
					pageSize: 10,
				}),
				method: 'POST',
				header: {
					'content-type': 'application/json',
					"Access-Control-Allow-Origin": '*',
				},
				success: (res) => {
					if (res.statusCode === 200 && res.data.code === 1) {
						const talkList = res.data.data;
						this.total = res.data.total;
						const len = talkList.length;
						for (let i = 0; i < len; i++) {
							talkList[i].webState = false;
						}
						this.talkList = this.talkList.concat(talkList);
					} else {
						uni.showToast({
							title: res.data && res.data.msg || res.data.error,
							icon: "none"
						});
					}
				}
			});
		},
		onConfirm(item) {
			this.value = false;
			this.replyInfo = `我是本周值班教师${this.teacherInfo && this.teacherInfo.name}，请问您想和我分享什么`;
			this.sendMassage(item);
		},
		sendMassage(item) {
			uni.request({
				url: CommApi.api.sendMassage.real,
				data: JSON.stringify({
					studentId: (this.userinfo && this.userinfo.id) || 63,
					teacherId: this.teacherInfo && this.teacherInfo.teacherid,
					content: item.content,
					isPublish: item.isPublish,
					isAnonymous: item.isAnonymous,
				}),
				method: 'POST',
				header: {
					'content-type': 'application/json',
					"Access-Control-Allow-Origin": '*',
				},
				success: (res) => {
					if (res.statusCode === 200 && res.data.code === 1) {
						this.page = 1;
						this.getTalkList();
					} else {
						uni.showToast({
							title: res.data && res.data.msg || res.data.error,
							icon: "none"
						});
					}
				}
			});
		},
	}
}
